
import { useAdmin } from '@/core/hooks/app'
import { defineComponent, onUnmounted, ref } from 'vue'
export default defineComponent({
    setup() {
        let timer: any = null
        const second = ref(5)
        const { router } = useAdmin()
        timer = setInterval(() => {
            if (second.value === 0) {
                clearInterval(timer)
                router.go(-1)
            } else {
                second.value--
            }
        }, 1000)
        onUnmounted(() => {
            clearInterval(timer)
        })

        return {
            second,
        }
    },
})
