
    import {
        defineComponent,
        reactive,
        onMounted,
    } from "vue";
    import {
        apiWorkbench
    } from '@/api/workbench'
    export default defineComponent({
        setup() {
            // 表单数据
            const workbenchData = reactive({
                version: {
                    version: '', // 版本号
                    website: '', // 官网
                },

                today: {}, // 今日数据
                menu: [], // 常用功能
                visitor: [], // 访问量
                article: [], // 文章阅读量
            })

            // 获取工作台主页数据
            const getWorkbench = () => {
                apiWorkbench()
                    .then((res: any) => {
                        console.log('res', res)
                        workbenchData.version = res.version
                        workbenchData.today = res.today
                        workbenchData.menu = res.menu
                        workbenchData.visitor = res.visitor
                        workbenchData.article = res.article
                    })
                    .catch((err: any) => {
                        console.log('err', err)
                    })
            }

            onMounted(() => {
                getWorkbench()
            })

            return {
                workbenchData,
                getWorkbench,
            }
        },
    })
